.container {
  max-width: 1440px;
  margin: 0 auto;
}

.title {
  color: #e0e0e0;
}

.form {
  margin-top: 20px;
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background-color: #e0e0e0;
  border-radius: 3px;
  overflow: hidden;
}

.formButton {
  display: inline-block;
  width: 48px;
  height: 48px;
  border: 0;
  background-image: url('https://cdn3.iconfinder.com/data/icons/feather-5/24/search-512.png');
  background-size: 40%;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #e0e0e0;
  opacity: 0.6;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  outline: none;
}

.formButton:hover {
  opacity: 1;
}

.formButtonLabel {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
}

.input {
  display: inline-block;
  width: 100%;
  font: inherit;
  font-size: 20px;
  border: none;
  outline: none;
  padding-left: 4px;
  padding-right: 4px;
  background-color: #e0e0e0;
}

.input::placeholder {
  font: inherit;
  font-size: 18px;
}
